import {
    Component,
    h,
    Host,
    Prop,
    State,
    Element,
    Event,
    EventEmitter,
} from '@stencil/core';
import { classlist } from '../../utils/classlist';

const inputVariantsConfig = {
    variants: {
        disabled: {
            false: 'bg-white',
            true: 'bg-surface-1 text-disabled border-none focus:ring-0',
        },
        readonly: {
            false: 'bg-white',
            true: 'text-dark border-none focus:ring-0',
        },
        invalid: {
            false: 'border-med focus:border-active-dark focus:ring-active-foreground/30 focus:ring-2',
            true: 'border-error-base focus:ring-destructive-foreground/30 focus:ring-2',
        },
    },
    compoundVariants: [
        {
            readonly: false,
            disabled: false,
            invalid: false,
            class: 'border-med text-dark focus:border-active-foreground focus:ring-active-foreground/30 bg-white focus:ring-2',
        },
    ],
    defaultVariants: {
        disabled: false,
        readonly: false,
        invalid: false,
    },
};

const getInputClasses = classlist(
    'align-center transition[background-color,color,box-shadow] text-body relative flex h-full w-full resize-none rounded-md border border-solid px-8 py-[6px] duration-300 ease-in-out outline-none',
    inputVariantsConfig,
);

/**
 * @category Form Controls
 * @description A multi-line text input component for longer text content with auto-resize and validation features. Includes label, hint text, character counting, and accessibility support.
 */
@Component({
    tag: 'at-textarea',
    shadow: false,
})
export class AtTextareaComponent {
    /**
     * Label to be displayed for the textarea
     */
    @Prop()
    label: string;

    /**
     * Short description or validation hint if required
     */
    @Prop()
    hint_text: string;

    /**
     * Optional info icon with detailed tooltip description
     */
    @Prop()
    info_text: string;

    /**
     * Displayed when invalid is set
     */
    @Prop()
    error_text: string;

    /**
     * Placeholder in the textarea when no text is placed
     */
    @Prop()
    placeholder: string;

    /**
     * Adds a required * to the label
     */
    @Prop()
    required: boolean;

    /**
     * Displays then error text when set
     */
    @Prop()
    invalid: boolean;

    /**
     * Makes the textarea read only
     */
    @Prop()
    readonly: boolean;

    /**
     * Disables interaction with the textarea
     */
    @Prop()
    disabled: boolean;

    /**
     * Minimum number of rows to display
     */
    @Prop()
    min_rows: number = 1;

    /**
     * Maximum number of rows to expand to
     */
    @Prop()
    max_rows: number = 10;

    /**
     * Sets the 'autocomplete' attribute on the textarea
     */
    @Prop()
    autocomplete: string = 'on';

    /**
     * Sets the value of the textarea
     */
    @Prop({ mutable: true })
    value: string;

    @State() textareaEl: HTMLTextAreaElement;
    @Element() el: HTMLAtTextareaElement;

    /**
     * Emits an event containing the text string when the textarea's content is changed
     */
    @Event()
    atuiChange: EventEmitter<string>;

    private textareaId = `textarea-${Math.random().toString(36).substring(2, 11)}`;

    handleChange(event: Event): void {
        event.stopPropagation();
        this.value = (event.target as HTMLInputElement).value;

        this.atuiChange.emit(this.value);
    }

    render() {
        const classname = getInputClasses({
            invalid: this.invalid,
            disabled: this.disabled,
            readonly: this.readonly,
        });

        return (
            <Host>
                <div class="flex flex-col">
                    <slot name="label" />
                    {(this.label || this.required || this.info_text) && (
                        <at-form-label
                            label={this.label}
                            for={this.textareaId}
                            required={this.required && !this.readonly}
                            info_text={this.info_text}
                        />
                    )}

                    {this.hint_text && (
                        <span
                            class="text-light mb-8 inline-block text-xs leading-tight"
                            data-name="textarea-hint"
                        >
                            {this.hint_text}
                        </span>
                    )}
                </div>

                <div class="grid">
                    <textarea
                        id={this.textareaId}
                        readonly={this.readonly ? true : undefined}
                        disabled={this.disabled ? true : undefined}
                        required={this.required}
                        placeholder={this.placeholder}
                        autocomplete={this.autocomplete}
                        autocapitalize="none"
                        onInput={(event) => {
                            this.value = (
                                event.target as HTMLInputElement
                            ).value;
                        }}
                        onChange={(event) => this.handleChange(event)}
                        value={this.value}
                        ref={(el) => (this.textareaEl = el)}
                        style={{ gridArea: '1 / 1 / 2 / 2' }}
                        class={classname}
                        data-name="textarea-input"
                    />
                    <div
                        data-name="textarea-resize-container"
                        style={{
                            gridArea: '1 / 1 / 2 / 2',
                            minHeight: `${this.min_rows * 1.5 + 1}rem`,
                            maxHeight: `${this.max_rows * 1.5 + 1}rem`,
                        }}
                        class="text-body invisible rounded-sm border px-8 py-[6px] whitespace-pre-wrap"
                    >
                        {this.value}
                    </div>
                </div>

                {this.error_text && this.invalid && (
                    <span
                        class="text-error text-sm"
                        data-name="textarea-error"
                    >
                        {this.error_text}
                    </span>
                )}
            </Host>
        );
    }
}
